@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
.title-container{background-color:goldenrod;text-align:center;padding:10px 0}@media screen and (max-width: 450px){.title-container .title{width:80%;margin:20px auto;font-size:40px}}@media screen and (max-width: 350px){.title-container .title{font-size:35px}}

.custom-button{height:50px;width:240px;margin:0 auto;font-size:x-large;background-color:goldenrod;border:solid black 1px;border-radius:5px;cursor:pointer}

.input-form{display:flex;flex-direction:column}.input-form .input-block{display:flex;flex-direction:row;justify-content:space-around;align-items:center;width:60%;margin:0 auto;margin-bottom:40px}.input-form .input-block input::-webkit-outer-spin-button,.input-form .input-block input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.input-form .input-block input[type='number']{-moz-appearance:textfield}.input-form .input-block .input{width:60px;height:60px;font-size:xx-large;text-align:center}@media screen and (max-width: 450px){.input-form .input-block{width:100%;padding:0 15px}.input-form .input-block .input{width:50px;height:50px;font-size:x-large;text-align:center}}

.input-container{display:flex;flex-direction:column;justify-content:center;margin-bottom:30px}.input-container .input-container_title{text-align:center}

.result-number-block{height:75px;width:75px;border:solid black 1px;border-radius:50%;text-align:center}.result-number-block .result-number{margin:0;margin-top:18px;font-size:xx-large}@media screen and (max-width: 450px){.result-number-block{height:55px;width:55px}.result-number-block .result-number{margin:0;margin-top:7px}}

.output-container{display:flex;flex-direction:row;justify-content:space-around;width:60%;margin:0 auto}@media screen and (max-width: 450px){.output-container{width:unset}}

* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Open Sans', sans-serif;
}

